.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.form{
  width: 20rem;
}

*:focus {
  outline: none;
}

.container-input{
  display: flex;
  flex-direction: column;
  height: 7rem;
}

.input-field{
  width: 20rem;
  height: 2rem;
  font-size: 18px;
  border: 1px solid black;
}

.input-error{
  border: 2px solid tomato
}

.input-error-label{
  color: tomato;
  font-size: 15px;
  text-align: left;
}

.input-label{
  height: 20px;
  width: 100%;
}